<template>
    <v-data-table
            :headers="headers"
            :items="clientes"
            :search="busqueda"
            class="my-2 mx-2"
    >
        <template v-slot:top>
            <v-toolbar flat color="indigo lighten-5">
                <v-toolbar-title>Todos Los Clientes</v-toolbar-title>
                <v-text-field
                        class="mx-8"
                        v-model="busqueda"
                        append-icon="mdi-magnify"
                        label="Buscar"
                        single-line
                        hide-details
                ></v-text-field>

                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                        <v-btn color="primary" dark class="mb-2" v-on="on">Nuevo Cliente</v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" >
                                        <v-text-field v-model="editedItem.nombre" label="Nombre"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6">
                                        <v-text-field v-model="editedItem.apellidos" label="Apellidos"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" >
                                        <v-text-field v-model="editedItem.dpi" label="DPI"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6">
                                        <v-text-field v-model="editedItem.telefono" label="Teléfono"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6">
                                        <v-text-field v-model="editedItem.nit" label="NIT"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6">
                                        <v-text-field v-model="editedItem.email" label="Correo Electrónico"></v-text-field>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                            <v-btn color="primary" dark text @click="save">Guardar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
            <v-icon
                    small
                    color="indigo accent-3"
                    class="mr-2"
                    @click="editItem(item)"
            >
                mdi-pencil
            </v-icon>
            <v-icon
                    small
                    color="red"
                    @click="deleteItem(item)"
            >
                mdi-delete
            </v-icon>
        </template>
        <template v-slot:no-data>
            <v-btn color="primary" @click="initialize">Reset</v-btn>
        </template>
    </v-data-table>
</template>

<script>
    export default {
        data: () => ({
            dialog: false,
            busqueda : '',
            headers: [
                {
                    text: 'Nombre',
                    align: 'start',
                    value: 'nombre',
                },
                { text: 'Apellidos', value: 'apellidos' },
                { text: 'DPI', value: 'dpi' },
                { text: 'Teléfono', value: 'telefono' },
                { text: 'NIT', value: 'nit' },
                { text: 'Correo Electrónico', value: 'email' },
                { text: 'Acciones', value: 'actions', sortable: false },
            ],
            clientes: [],
            editedIndex: -1,
            editedItem: {
                nombre: '',
                apellidos: '',
                dpi: '',
                telefono: '',
                nit: '',
                email : '',
            },
            defaultItem: {
                nombre: '',
                apellidos: '',
                dpi: '',
                telefono: '',
                nit: '',
                email : '',
            },
        }),
        computed: {
            formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Producto' : 'Editar Producto'
            },
        },

        watch: {
            dialog (val) {
                val || this.close()
            },
        },

        created () {
            this.initialize()
        },

        methods: {
            initialize () {
                this.clientes = [
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Marcos Mauricio',
                        apellidos: 'Rivas López',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    },
                    {
                        nombre: 'Miguel Estuardo',
                        apellidos: 'Tellez Morales',
                        dpi: '2431588781301',
                        telefono: '45458585',
                        nit: '3025487-8',
                        email: 'correo@gmail.com',
                    }
                ]
            },

            editItem (item) {
                this.editedIndex = this.clientes.indexOf(item)
                this.editedItem = Object.assign({}, item)
                this.dialog = true
            },

            deleteItem (item) {
                const index = this.clientes.indexOf(item)
                confirm('¿ Seguro Que Desea Eliminar Este Registro ?') && this.clientes.splice(index, 1)
            },

            close () {
                this.dialog = false
                setTimeout(() => {
                    this.editedItem = Object.assign({}, this.defaultItem)
                    this.editedIndex = -1
                }, 300)
            },

            save () {
                if (this.editedIndex > -1) {
                    Object.assign(this.clientes[this.editedIndex], this.editedItem)
                } else {
                    this.clientes.push(this.editedItem)
                }
                this.close()
            },
        },
    }
</script>